<template>
  <div class="container-water-fall">
    <waterfall :col=2 :data="goods" :lazyDistance=50>
      <template>
        <div class="item" v-for="(item,index) in goods" :key="index">
          <a :href="item.link">
            <img class="item-img" v-if="showImage(item)" v-lazy="showImage(item)" alt="加载错误" @load="imageLoad">
            <p class="item-text">{{item.title}}</p>
          </a>
        </div>
      </template>
    </waterfall>
  </div>
</template>

<script>
  export default {
    name: "CateGoodList",
    props:{
      goods: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data(){
      return{
        col:3,
        loading:false,
      }
    },
    computed: {
    },
    methods: {
      imageLoad() {
        this.$bus.$emit('imageLoad')
      },
      showImage(item) {
        return item.image || item.show.img
      }
    }
  }
</script>

<style scoped>
  .item {
    text-align: center;
    font-size: 12px;
  }

  .item-img {
    width: 50%;
  }

  .item-text {
    margin-top: 5px;
    margin-bottom: 10px;
  }
</style>