<template>
  <div id="category">
    <nav-bar class="category-nav">
      <template v-slot:center><div>商品分类</div></template>
    </nav-bar>
    <div class="content">
      <tab-menu :categories="categories"
                @selectItem="selectItem">
      </tab-menu>
      <scroll id="tab-content" :data="[categoryData]">
        <tab-content-category :subcategories="showSubcategory"></tab-content-category>
        <tab-control :titles="['综合', '新品', '销量']" @itemClick="tabClick"></tab-control>
        <tab-content-detail :category-detail="showCategoryDetail"></tab-content-detail>
      </scroll>
    </div>
  </div>
</template>

<script>
  import NavBar from "components/common/navbar/NavBar";
  import TabMenu from "./childCompos/TabMenu";
  import Scroll from "components/common/scroll/Scroll";
  import TabContentCategory from "./childCompos/TabContentCategory";
  import TabControl from "components/content/tabControl/TabControl";
  import TabContentDetail from "./childCompos/TabContentDetail";

  import {getCategory, getSubcategory, getCategoryDetail} from "network/category";
  import {tabControlMixin} from "@/common/mixin";
  import {debounce} from "common/utils";
  export default {
    name: "Category",
    components: {
      NavBar,
      TabMenu,
      Scroll,
      TabContentCategory,
      TabControl,
      TabContentDetail
    },
    mixins: [tabControlMixin],
    data() {
      return {
        categories: [],
        categoryData: {},
        currentIndex: -1
      }
    },
    created() {
      //请求数据
      getCategory().then(res => {
        this.categories = res.data.category.list
        // 2.初始化每个类别的子数据
        for (let i = 0; i < this.categories.length; i++) {
          this.categoryData[i] = {
            subcategories: {},
            categoryDetail: {
              'pop': [],
              'new': [],
              'sell': []
            }
          }
        }
        // 3.请求第一个分类的数据
        this.getSubcategories(0)
      })
    },
    mounted() {
      const refresh = debounce(this.$refs.scroll.refresh,300)
      //监听图片加载完成
      this.$bus.$on('imageLoad',() => {
        refresh()
      })
    },
    computed: {
      showSubcategory() {
        if (this.currentIndex === -1) return {}
        return this.categoryData[this.currentIndex].subcategories
      },
      showCategoryDetail() {
        if (this.currentIndex === -1) return []
        return this.categoryData[this.currentIndex].categoryDetail[this.currentType]
      }
    },
    methods: {
      getSubcategories(index) {
        this.currentIndex = index;
        const mailKey = this.categories[index].maitKey;
        getSubcategory(mailKey).then(res => {
          this.categoryData[index].subcategories = res.data
          this.categoryData = {...this.categoryData}
          this.getCategoryDetail('pop')
          this.getCategoryDetail('sell')
          this.getCategoryDetail('new')
        })
      },
      getCategoryDetail(type) {
        // 1.获取请求的miniWallkey
        const miniWallkey = this.categories[this.currentIndex].miniWallkey;
        // 2.发送请求,传入miniWallkey和type
        getCategoryDetail(miniWallkey, type).then(res => {
          // 3.将获取的数据保存下来
          this.categoryData[this.currentIndex].categoryDetail[type] = res
          this.categoryData = {...this.categoryData}
        })
      },
      selectItem(index) {
        this.getSubcategories(index)
      }
    },
  }
</script>

<style scoped>
  .category-nav {
    background-color: var(--color-tint);
    color: #fff;
    z-index: 9;
  }
  #category {
    height: 100vh;
  }
  .content {
    position: absolute;
    left: 0;
    right: 0;
    top: 44px;
    bottom: 49px;
    display: flex;
    overflow: hidden;
  }

  #tab-content {
    width: calc(100% - 100px);
    height: 100%;
    flex: 1;
  }
</style>